.welcome__link:active {
  color: #40a2ae;
  text-shadow: 1px 1px #78c4cd;
}

@media only screen and (min-width: 1024px) {
  .btn-box__link {
    transition: all .3s ease;
  }

  .btn-box__link:hover,
  .btn-box__link:focus {
    background: #d5e4f1;
  }

  .btn-box__link:active {
    background: #b0dce2;
  }

  .btn-box__link.is-active:hover,
  .btn-box__link.is-active:focus {
    background: #96d1d8;
  }

  .btn-box__link.is-active:active {
    background: #43a9b5;
  }

  .navigation__link {
    transition: all .3s ease;
  }

  .navigation__link:hover,
  .navigation__link:focus {
    color: #fff;
    background-color: #78c4cd;
    box-shadow: 0 -2px 0 1px #71c1ca;
  }

  .user-menu__item:last-child .user-menu__link:hover,
  .user-menu__item:last-child .user-menu__link:focus {
    background-color: #78c4cd;
  }

  .user-menu__item:last-child .user-menu__link:active {
    background-color: #53b4bf;
  }

  .user-menu__link {
    transition: all .3s ease;
  }

  .user-menu__link:hover,
  .user-menu__link:focus {
    color: #fff;
    background-color: #78c4cd;
  }

  .welcome__link {
    transition: all .2 ease;
  }

  .welcome__link:hover,
  .welcome__link:focus {
    text-shadow: 1px 1px #78c4cd;
  }
}

@media only screen and (min-width: 1205px) {
  html {
    font-size: 15px;
  }
}

@media only screen and (max-width: 992px) {
  .header__top,
  .header__bottom-container,
  .header__navigation,
  .content {
    padding-left: 50px;
    padding-right: 50px;
  }
}

@media only screen and (max-width: 768px) {
  .header__top,
  .header__bottom-container,
  .header__navigation,
  .content {
    padding-left: 20px;
    padding-right: 20px;
  }

  .promo {
    justify-content: space-around;
  }

  .promo__item {
    width: 50%;
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }

  * {
    transition: none !important;
  }
}